html,body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}
.ant-layout{
  min-height:100vh;
  height:100%!important;  
}
.ant-layout-content{
  height:auto !important;
}


.app .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  -webkit-transition: color .3s;
  transition: color .3s;
}

.app .trigger:hover {
  color: #1890ff;
}

.app .logo {
  height: 32px;
  background: rgba(255,255,255,.2);
  margin: 16px;
  text-align: center;
  color:#fff;
  line-height: 32px;
  font-size: 28px;
}
.ant-layout{
  height:100vh;
}
a{
  text-decoration: none;
  color:#fff;
}
.login-out{
  position: absolute;
  top:0;
  right:0;
  padding:0 20px;
  background: rgb(240, 87, 27);
  color:#fff;
  font-size:16px;
  cursor:pointer;
}






.ant-carousel .slick-slide {
    text-align: center;
    height: 300px;
    line-height: 300px;
    background: #364d79;
    overflow: hidden;
  }
  
  .ant-carousel .slick-slide h3 {
    color: #fff;
   
  }
.ant-upload-select-picture-card i {
    font-size: 32px;
    color: #999;
  }
  
  .ant-upload-select-picture-card .ant-upload-text {
    margin-top: 8px;
    color: #666;
  }
.bg{
    width:100%;
    height:100vh;
    background: #041625;
}
.box-from{
    width:350px;
    height:300px;
    background: #fff;
    border-radius: 5px;
    position: absolute;
    top:50%;
    left:50%;
    margin-left:-175px;
    margin-top:-250px;
}
.from-content{
    position: relative;
    top:30px;
    left:25px;
}
.box-from .login-form {
    max-width: 300px;
  }
.box-from .login-form-forgot {
    float: right;
  }
  .box-from .login-form-button {
    width: 100%;
  }
